.TopNav {
  width: 100%;
  display: flex;
  justify-content: center;
  box-sizing: border-box;
  background-color:rgb(13, 27, 37);
  .item{
    width: 100%;
    display: flex;
    -webkit-box-pack: justify;
    -ms-flex-pack: justify;
    justify-content: space-between;
    padding: 1.2rem 1.6rem;
  }
  .logobox{
    
  .link{
    text-decoration: none;
  }
    .logo{
      text-align: center;
      width: 20rem;
      height: 2rem;
      margin: 4px 0;
      font-size:30px;
      font-family: 'GONGBO';
      color:white;
    }
    .explain{
      text-align: center;
      width: 20rem;
      height: 1rem;
      margin: 4px 0;
      font-size:15px;
      font-family: 'GONGBO';
      color:white;
    }
    
  }
  .right{
    
    margin-left: 80px;
    width: 15rem;
    display: flex;
    .loginButton{
      border: 1px solid white!important;
      font-family: 'GONGRE';
      border-radius: 4px;
      background-color: rgb(13, 27, 37) !important;
      color: white!important;
      font-weight: 400;
      text-decoration: none;
      height: 100%;
      font-size: 0.8rem;
      text-align: center;
      line-height: 3;
      vertical-align: middle;
      cursor: pointer;
      width: 6rem;
      margin-left: 4px;
      width: 7.2rem;
    }
    .nickname{
      font-family: 'GONGRE';
      color: white!important;
      font-weight: 400;
      text-decoration: none;
      height: 100%;
      font-size: 0.9rem;
      text-align: center;
      cursor: pointer;
      margin: 14px 0 0 0px;
      margin-right:1.5rem;
    }
  }
  
}

nav{
  float: left;
  margin: 10px 0 0 0px;
  overflow: hidden;
  a{
    font-size: 15px;
    font-weight: 500;
    text-decoration: none;
    color: #fff;
    margin-left: 80px;
    font-family: 'GONGRE';
    font-size: 1rem;
  }
  a:first-child {
    margin-left: 0;
  }
}
.modal{
  padding: 3.2rem 2.4rem;
  display: block;
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  font-family: 'GONGRE';
  text-decoration: none;
  .logo{
    margin-top:10px;
    font-size:35px;
    font-family: 'GONGBO';
    text-align: center;
    color:rgb(14, 69, 115);
  }
  .menu{
    width: 20rem;
    height: 4.4rem;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    position: relative;
    margin: 0.5rem auto 1.6rem;
    .abled{
      width: 18rem;
      line-height: 1.5rem;
      font-size: 1.1rem;
      font-weight: 500;
      color: rgb(14, 69, 115);
      border-bottom: 1px solid #e8e6e6;
      background-color: #fff;
    }
    .disabled{
      width: 18rem;
      line-height: 1.5rem;
      font-size: 1.1rem;
      font-weight: 500;
      color: #989696;
      border-bottom: 1px solid #e8e6e6;
      background-color: #fff;
    }
    button{
      display: inline-block;
      font-family: inherit;
      vertical-align: middle;
      cursor: pointer;
      white-space: nowrap;
      text-decoration: none;
      background: transparent;
      border: 0;
      box-shadow: none!important;
    }
    
    .leftline{
      width: 10rem;
      height: 2px;
      position: absolute;
      bottom: 0;
      left: 0;
      -webkit-transition: .5s;
      transition: .5s;
      background-color: rgb(14, 69, 115);
      transform: translateX(0rem);
    }
    .rightline{
      width: 10rem;
      height: 2px;
      position: absolute;
      bottom: 0;
      left: 10rem;
      -webkit-transition: .5s;
      transition: .5s;
      background-color: rgb(14, 69, 115);
      transform: translateX(0rem);
    }
  }
  .login{
    width: 100%;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -ms-flex-direction: column;
    flex-direction: column;
    margin: 0;
    padding: 0;
    border: 0;
    font-size: 100%;
    font: inherit;
    font-family: 'GONGRE';
    text-decoration: none;
    input{
      width: 100%;
      height: 3.5rem;
      position: relative;
      bottom: 0;
      left: 0;
      border: 0;
      margin-bottom: 30px;
      border-bottom: 1px solid rgb(14, 69, 115);
      line-height: 1.4rem;
      font-size: 1.1rem;
      font-weight: 500;
      -webkit-appearance: none;
      -webkit-border-radius: 0;
      -moz-appearance: none;
      appearance: none;
      border-radius: 0;
      background-image: none;
      background-color: #fff;
      color: #272424;
      
      resize: none;
      box-shadow: none!important;
    }
    input:focus {outline:none;}
    
  }
  .warning{
    margin-bottom: 20px;
    text-align: center;
    color:#BB2E3E;
  }
  
  .loginButton{
    width: 100%;
    height: 2.5rem;
    background:  rgb(14, 69, 115);
    color: #fff;
    border-radius: 0.6rem;
    font-size: 1.6rem;
    font-family: 'GONGLI';
    margin: 0rem auto 0.8rem;
  }
}


.ReactModal__Overlay--after-open {
  background-color: rgba(0, 0,0, 0.75);
}
.mymodal {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border: 1px solid #ccc;
  background: #fff;
  overflow: auto;
  border-radius: 4px;
  outline: none;
  padding: 20px;
}
.ReactModal__Overlay {
  opacity: 0;
  transition: opacity 500ms ease-in-out;
}

.ReactModal__Overlay--after-open {
  opacity: 0;
}

.ReactModal__Overlay--before-close {
  opacity: 0;
}
.myoverlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.75);
}